<template>
	<div class="trading-list">
		<el-collapse v-model="activeNames">
			<div class="row">
				<template v-for="(li, idx) in list" :key="li">
					<el-collapse-item :title="li.title" :name="idx" class="item" v-if="isMobile?true:idx%2===0">
						<template #title>
							<img src="@/assets/images/common/add.png" alt="" class="more-btn"
							     @change="toggle">
							{{ t(li.title) }}
						</template>
						<div class="item-more">{{ t(li.cxt) }}</div>
					</el-collapse-item>
				</template>
			</div>
			<div class="row" v-if="!isMobile">
				<template v-for="(li, idx) in list" :key="li">
					<el-collapse-item :title="li.title" :name="idx" class="item" v-if="idx%2!==0">
						<template #title>
							<img src="@/assets/images/common/add.png" alt="" class="more-btn"
							     @change="toggle">
							{{ t(li.title) }}
						</template>
						<div class="item-more">{{ t(li.cxt) }}</div>
					</el-collapse-item>
				</template>
			</div>
		</el-collapse>
	</div>
</template>
<script setup>
import {ref} from 'vue';
import {ElCollapse, ElCollapseItem} from "element-plus"
import {useCheckMobile} from "@/store/isMobile";

const isMobile = computed(()=>{
	return useCheckMobile().getItem()
})
const {t} = useI18n();
let activeNames = ref([-1]);
defineProps({
	list: {
		type: Array,
		default: () => []
	}
})

const toggle = (idx) => {
	console.log(idx, "000");
}
</script>
<style scoped lang='scss' src="./index.scss">
</style>